.container{
    display: flex;
}
.messagewindow{
    display: flex;
    max-width: 220px;
    min-height: 50px;
    background-color: #082103;
    padding: 5px;
    border-radius: 5px;
    position: relative;
}
.messagewindow::before{
    content: "";
    position: absolute;
    border-color: #082103 transparent transparent #082103;
    border-style: solid;
    border-width: 5px;
    left: 0;
    bottom: -6px;
}

.avatar img{
    width:60px;
    border-radius: 25px;
    margin: 0 10px;
}
.message{
    color: #B0C4B0;
}
.name{
    color:#9E9BB6 ;
}
.time{
    color: #344D30;
    display: flex;
    align-items: flex-end;
}
